import Link from "next/link";

export const Table = ({
  columns,
  data,
  getRowLink,
}: {
  columns: { Header: string; accessor: string; hiddenOnSmall?: boolean }[];
  data: any[];
  getRowLink?: (item: any, index: number) => string; // Optional link generator
}) => {
  return (
    <div className="rounded-lg border border-gray-200 bg-white shadow-lg dark:border-gray-700 dark:bg-gray-800 overflow-hidden">
      <div className="flex flex-col">
        {/* Table Header */}
        <div className="grid grid-cols-3 rounded-t-lg bg-gradient-to-r from-blue-50 to-purple-50 dark:from-gray-700 dark:to-gray-700 sm:grid-cols-5">
          {columns.map((col, index) => (
            <div
              key={index}
              className={`p-3 xl:p-4 ${
                col.hiddenOnSmall ? 'hidden sm:block' : ''
              }`}
            >
              <h5 className="text-sm font-semibold uppercase tracking-wide text-gray-700 dark:text-gray-200">
                {col.Header}
              </h5>
            </div>
          ))}
        </div>

        {/* Table Rows */}
        {data.map((item, index) => {
          const rowContent = (
            <div
              className={`grid grid-cols-3 sm:grid-cols-5 transition-all duration-200 ease-in-out ${
                getRowLink ? 'hover:bg-gray-50 dark:hover:bg-gray-700 cursor-pointer' : ''
              } border-b border-gray-100 dark:border-gray-700`}
              key={index}
            >
              {columns.map((col, colIndex) => (
                <div
                  key={colIndex}
                  className={`p-3 xl:p-4 ${
                    col.hiddenOnSmall ? 'hidden sm:block' : ''
                  }`}
                >
                  <p
                    className={`text-sm font-medium ${
                      item[col.accessor] === 'Inactive'
                        ? 'bg-red-100 text-red-800 px-2 py-1 rounded-full font-semibold text-xs w-fit'
                        : item[col.accessor] === 'Active'
                        ? 'bg-green-100 text-green-800 px-2 py-1 rounded-full font-semibold  text-xs w-fit'
                        : 'text-gray-700 dark:text-gray-200'
                    }`}
                  >
                    {item[col.accessor]}
                  </p>
                </div>
              ))}
            </div>
          );

          // If a link generator is provided, wrap the row in a Link component
          return getRowLink ? (
            <Link href={getRowLink(item, index)} key={index}>
              {rowContent}
            </Link>
          ) : (
            rowContent
          );
        })}
      </div>
    </div>
  );
};

